.tb {
	&--a {
		border-radius: 15%;
		width: 40px;
		height: 40px;
		overflow: hidden; } }
.footer--sites {
	.tb {
		&--a {
			border-radius: 50%; } } }
.smallSiteList--thumb {
	width: 40px;
	height: 40px;
	display: block;
	background-position: var(--p, 100% 100%);
	background-color: var(--b, red);
	background-image: url("../img/thumb_.jpg");
	filter: contrast(2); }


@each $site in $thumb-list {
	$x: nth($site, 2);
	$y: nth($site, 3);
	.#{nth($site, 1)} {
		//background: linear-gradient(135deg , #280047 0% , black 50% , $branding-primary 100%)
		--p: $x $y; } }
